import { Keypair } from "@solana/web3.js";
import bs58 from "bs58";
import nacl from "tweetnacl";
import SolanaWallet, { SolanaSignTransaction } from "./solanawallet";
import { Buffer } from "buffer";

/**
 * Types
 */
interface IInitArguments {
  secretKey?: Uint8Array;
}
/**
 * Library
 */
export default class SolanaLib {
  keypair: Keypair;
  solanaWallet: SolanaWallet;

  constructor(secreatKey: Uint8Array) {
    this.keypair = Keypair.fromSecretKey(secreatKey);
    console.log("secreatKey", secreatKey);

    this.solanaWallet = new SolanaWallet(Buffer.from(secreatKey));
  }

  public async getAddress() {
    return await this.keypair.publicKey.toBase58();
  }

  public getSecretKey() {
    return this.keypair.secretKey.toString();
  }

  public async signMessage(message: string) {
    const signature = nacl.sign.detached(
      bs58.decode(message),
      this.keypair.secretKey
    );
    const bs58Signature = bs58.encode(signature);

    return { signature: bs58Signature };
  }

  public async signTransaction(params: SolanaSignTransaction) {
    const { feePayer, recentBlockhash, instructions, partialSignatures } =
      params;
    console.log("signTransaction params", params);

    const { signature, serializeMessage } =
      await this.solanaWallet.signTransaction(feePayer, {
        feePayer,
        instructions,
        recentBlockhash,
        partialSignatures: partialSignatures ?? [],
      });
    console.log("signature", signature);

    return { signature, serializeMessage };
  }
}
