import Wallet from "./SolanaLib";
import React from "react";
import { useSearchParams } from "react-router-dom";
import bs58 from "bs58";
import sampleData from "./test.json";
import { SolanaSignTransaction } from "./solanawallet";

declare global {
  interface Window {
    SafeMoon: any; // this will be your variable name
  }
}
function isJson(str: any) {
  try {
    JSON.parse(str);
  } catch (e) {
    console.error("isJson", e);

    return false;
  }
  return isNaN(str);
}
export default function App() {
  const [searchParams] = useSearchParams();

  const init = async () => {
    try {
      const secreatKeyinBase58 = searchParams.get(
        "secreatKeyinBase58"
      ) as string;
      console.log(secreatKeyinBase58);
      if (!secreatKeyinBase58) {
        throw new Error("Not valid secreatKey");
      }
      const secreatKey = bs58.decode(secreatKeyinBase58);
      console.log("secreatKey", secreatKey);
      // const
      const wallet = new Wallet(secreatKey);
      const signTransactionPayload = searchParams.get("signTransactionPayload");
      if (!signTransactionPayload) {
        throw new Error("Not valid Sign Transaction Payload");
      }
      console.log("signTransactionPayload", isJson(signTransactionPayload));

      const signTransactionParams = JSON.parse(
        signTransactionPayload.toString()
      );
      let partialSignatures = [];
      if (signTransactionParams.hasOwnProperty("signatures")) {
        partialSignatures = signTransactionParams["signatures"]
          .map((sign: any) => {
            if (sign.signature == null) {
              return null;
            } else {
              return {
                pubkey: sign.publicKey,
                signature: bs58.encode(sign.signature.data),
              };
            }
          })
          .filter((it: any) => it !== null);
      }

      console.log("partialSignatures", JSON.stringify(partialSignatures));

      const newSolanaSignTransaction = {
        partialSignatures,
        ...signTransactionParams,
      };
      const res = await wallet.signTransaction(
        newSolanaSignTransaction as unknown as SolanaSignTransaction
      );
      console.log("res", res.signature);
      window?.SafeMoon?.signPartialByBrijeshTransaction(res.signature);
      window?.SafeMoon?.serializedMessageByBrijeshTransaction(res.serializeMessage);
      return res;
    } catch (error) {
      console.error("", error);

      return null;
    }
  };

  React.useEffect(() => {
    try {
      console.log("hi", init());
      // window.SafeMoon.signTransaction(init());
    } catch (_) {}
  }, []);

  return (
    <div className="App">
      <h1>Hello</h1>
    </div>
  );
}
